import React, {Component} from 'react';
import {Tabs, Tab, Grid, Cell, Card, CardText, CardTitle, CardActions,Button, IconButton,CardMenu} from 'react-mdl';

class Projects extends Component {
    constructor(props){
        super(props);
        this.state = {activeTab: 0};
    }

    toggleCategories(){
        if(this.state.activeTab === 0 ){
            return(
                <div className="projects-grid">
                <Card shadow={6} style={{minWidth: '450px', margin: 'auto'}}>
                    <CardTitle style={{color: '#fff', height: '176px', background:'blue'}}>Random Quote Machine</CardTitle>
                      <CardText>
                          My first React Project, it's a random quote machine...
                      </CardText>
                      <CardActions border>
                          <Button colored>GitHub</Button>
                      </CardActions>
                      <CardMenu style={{color: '#fff0'}}>
                       <IconButton name="share" />
                      </CardMenu>
                </Card>
                <Card shadow={6} style={{minWidth: '450px', margin: 'auto'}}>
                    <CardTitle style={{color: '#fff', height: '176px', background:'yellow'}}>Random Quote Machine</CardTitle>
                      <CardText>
                          My first React Project, it's a random quote machine...
                      </CardText>
                      <CardActions border>
                          <Button colored>GitHub</Button>
                      </CardActions>
                      <CardMenu style={{color: '#fff0'}}>
                       <IconButton name="share" />
                      </CardMenu>
                </Card>
                <Card shadow={6} style={{minWidth: '450px', margin: 'auto'}}>
                    <CardTitle style={{color: '#fff', height: '176px', background:'yellow'}}>Random Quote Machine</CardTitle>
                      <CardText>
                          My first React Project, it's a random quote machine...
                      </CardText>
                      <CardActions border>
                          <Button colored>GitHub</Button>
                      </CardActions>
                      <CardMenu style={{color: '#fff0'}}>
                       <IconButton name="share" />
                      </CardMenu>
                </Card>
                </div>
            )

        }
        else if(this.state.activeTab === 1) {
            return (
                <div><h1>This is JavaScript</h1></div>
            )
        }
        else if(this.state.activeTab === 2){
            return(
                <div><h1>This is Angular</h1></div>
            )
        }
        else if(this.state.activeTab === 3){
            return(
                <div><h1>This is Vue JS </h1></div>
            )
        }
        else if(this.state.activeTab === 4){
            return(
                <div><h1>This is MongoDB</h1></div>
            )
        }

    }


    render(){
        return(
            <div className="category-tabs">
                <Tabs activeTab={this.state.activeTab}
                 onChange={(tabId) => 
                 this.setState({
                     activeTab: tabId})} ripple>
                 <Tab>React</Tab>
                 <Tab>JavaScript</Tab>
                 <Tab>Angular</Tab>
                 <Tab>Vue JS</Tab>
                 <Tab>MongoDB</Tab>
                </Tabs>
                <section>
                    <Grid>
                        <Cell col={12}>
                            <div className="content">{this.state.toggleCategories} </div>

                        </Cell>
                    </Grid>
                    {this.toggleCategories()}
                </section>
                
            </div>
        )
    }
}

export default Projects;